'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function FAQPage() {
  const [openIndex, setOpenIndex] = useState<number | null>(0);

  const faqs = [
    { q: 'What is Auxite?', a: 'Platform for tokenized precious metals. 1 token = 1 gram physical metal in LBMA vaults. Fully on-chain.' },
    { q: 'Are metals physically backed?', a: 'Yes, 100%. Every token backed by allocated metal in LBMA-approved vaults. Real metal, not paper.' },
    { q: 'What metals available?', a: 'Gold (AUXG), Silver (AUXS), Platinum (AUXPT), Palladium (AUXPD).' },
    { q: 'Are staking rewards guaranteed?', a: 'No. Protocol-based incentives, NOT guaranteed. Auxite does not lend.' },
    { q: 'Can I withdraw early?', a: 'No. Staked tokens locked for full duration.' },
    { q: 'Soft launch limits?', a: '$10,000/day buy/sell. 3-month staking only.' },
    { q: 'How to verify reserves?', a: 'Trust Center shows proof of reserves and on-chain supply data.' },
  ];

  return (
    <>
      <section className="mobile-pt" style={{ paddingTop: '140px', paddingBottom: '60px' }}>
        <div className="mobile-padding" style={{ maxWidth: '600px', margin: '0 auto', padding: '0 24px' }}>
          <div style={{ textAlign: 'center', marginBottom: '32px' }}>
            <h1 className="section-title" style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '32px', fontWeight: 700, color: 'white', marginBottom: '8px', letterSpacing: '-0.02em' }}>
              <span className="text-gold-gradient">FAQ</span>
            </h1>
            <p style={{ fontSize: '14px', color: '#71717A' }}>Everything about Auxite.</p>
          </div>

          <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
            {faqs.map((faq, index) => (
              <div key={index} style={{ 
                background: 'rgba(255,255,255,0.02)',
                border: '1px solid rgba(255,255,255,0.06)',
                borderRadius: '12px',
                overflow: 'hidden'
              }}>
                <button 
                  onClick={() => setOpenIndex(openIndex === index ? null : index)} 
                  style={{ 
                    width: '100%', 
                    padding: '14px 16px', 
                    background: 'transparent',
                    border: 'none',
                    cursor: 'pointer',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                    gap: '12px'
                  }}
                >
                  <span style={{ fontFamily: "'Space Grotesk', sans-serif", fontWeight: 600, color: 'white', fontSize: '14px', textAlign: 'left' }}>{faq.q}</span>
                  <svg style={{ 
                    width: '16px', 
                    height: '16px', 
                    color: '#D4AF37', 
                    flexShrink: 0,
                    transform: openIndex === index ? 'rotate(180deg)' : 'rotate(0)',
                    transition: 'transform 0.2s'
                  }} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                  </svg>
                </button>
                {openIndex === index && (
                  <div style={{ padding: '0 16px 14px 16px' }}>
                    <p style={{ color: '#A1A1AA', fontSize: '13px', lineHeight: 1.6, margin: 0 }}>{faq.a}</p>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </section>

      <section style={{ padding: '40px 0', background: '#080B14' }}>
        <div className="mobile-padding" style={{ maxWidth: '400px', margin: '0 auto', padding: '0 24px', textAlign: 'center' }}>
          <h2 style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '18px', fontWeight: 600, color: 'white', marginBottom: '16px' }}>Still have questions?</h2>
          <Link href="/contact" style={{ 
            display: 'inline-flex', 
            alignItems: 'center', 
            gap: '6px', 
            background: 'linear-gradient(135deg, #D4AF37 0%, #B8960C 100%)', 
            color: '#0A0F1C', 
            padding: '12px 24px', 
            borderRadius: '10px', 
            fontWeight: 600, 
            fontSize: '14px', 
            textDecoration: 'none'
          }}>
            Contact Us
          </Link>
        </div>
      </section>
    </>
  );
}
